/* eslint-disable no-undef */
import Cookies from 'js-cookie'

const TokenKey = 'Admin-Token'
const AccountKey = 'Account'
const UserTypeKey = 'UserTypeKey'

export function getToken() {
  return Cookies.get(TokenKey)
}

export function setToken(token: string) {
  return Cookies.set(TokenKey, token)
}

export function removeToken() {
  return Cookies.remove(TokenKey)
}

export function getAccount() {
  return Cookies.get(AccountKey)
}

export function setAccount(account: string) {
  return Cookies.set(AccountKey, account)
}

export function removeAccount() {
  return Cookies.remove(AccountKey)
}

export function getUserType() {
  return Cookies.get(UserTypeKey)
}

export function setUserType(userType: string) {
  return Cookies.set(UserTypeKey, userType)
}

export function removeUserType() {
  return Cookies.remove(UserTypeKey)
}

export function signoutCognito() {
  const poolData = {
    UserPoolId: process.env.VUE_APP_COGNITO_POOL_ID,
    ClientId: process.env.VUE_APP_COGNITO_CLIENT_ID
  }
  const userPool = new AWSCognito.CognitoIdentityServiceProvider.CognitoUserPool(
    poolData
  )
  const cognitoUser = userPool.getCurrentUser()
  if (cognitoUser !== null) {
    cognitoUser.signOut()
  }
}

export function getSession() {
  return new Promise((resolve: any, reject: any) => {
    const poolData = {
      UserPoolId: process.env.VUE_APP_COGNITO_POOL_ID,
      ClientId: process.env.VUE_APP_COGNITO_CLIENT_ID
    }
    const userPool = new AWSCognito.CognitoIdentityServiceProvider.CognitoUserPool(
      poolData
    )
    const cognitoUser = userPool.getCurrentUser()
    if (cognitoUser !== null) {
      cognitoUser.getSession((err: any, data: any) => {
        if (err) {
          // Prompt the user to reauthenticate by hand...
          reject(err)
        } else {
          const cognitoUserSession = data
          setToken(cognitoUserSession.accessToken.jwtToken)
          resolve()
        }
      })
    }
  })
}

export function authentation(userInfo: { username: string; password: string }) {
  return new Promise((resolve, reject) => {
    const poolData = {
      UserPoolId: process.env.VUE_APP_COGNITO_POOL_ID,
      ClientId: process.env.VUE_APP_COGNITO_CLIENT_ID
    }

    const authenticationData = {
      Username: userInfo.username,
      Password: userInfo.password
    }

    const userPool = new AWSCognito.CognitoIdentityServiceProvider.CognitoUserPool(
      poolData
    )
    const cognitoUser = userPool.getCurrentUser()
    const authenticationDetails = new AWSCognito.CognitoIdentityServiceProvider.AuthenticationDetails(
      authenticationData
    )
    if (cognitoUser !== null) {
      cognitoUser.authenticateUser(authenticationDetails, {
        onSuccess: (result: any) => {
          resolve(result)
        },

        onFailure: (err: any) => {
          reject(err)
        }
      })
    } else {
      reject(new Error())
    }
  })
}

export function loginCognito(userInfo: {
  username: string
  password: string
  newPassword?: string
}) {
  return new Promise((resolve, reject) => {
    const username = userInfo.username.trim().toLowerCase()
    const poolData = {
      UserPoolId: process.env.VUE_APP_COGNITO_POOL_ID,
      ClientId: process.env.VUE_APP_COGNITO_CLIENT_ID
    }
    let userPool
    try {
      userPool = new AWSCognito.CognitoIdentityServiceProvider.CognitoUserPool(
        poolData
      )
    } catch (error) {
      // console.log(error);
    }

    const authenticationData = {
      Username: username,
      Password: userInfo.password
    }

    const authenticationDetails = new AWSCognito.CognitoIdentityServiceProvider.AuthenticationDetails(
      authenticationData
    )

    const userData = {
      Username: username,
      Pool: userPool
    }

    const cognitoUser = new AWSCognito.CognitoIdentityServiceProvider.CognitoUser(
      userData
    )
    cognitoUser.authenticateUser(authenticationDetails, {
      onSuccess: (result: any) => {
        const authResult = result
        setToken(authResult.accessToken.jwtToken)
        setAccount(username)
        resolve({ token: getToken() })
      },

      onFailure: (err: any) => {
        reject(err)
      },

      newPasswordRequired(userattributes: any) {
        // test
        delete userattributes.email_verified
        cognitoUser.completeNewPasswordChallenge(
          userInfo.newPassword,
          userattributes,
          this
        )
      }
    })
  })
}

export function forgetPassord(username: any) {
  return new Promise((resolve, reject) => {
    const poolData = {
      UserPoolId: process.env.VUE_APP_COGNITO_POOL_ID,
      ClientId: process.env.VUE_APP_COGNITO_CLIENT_ID
    }

    let userPool
    try {
      userPool = new AWSCognito.CognitoIdentityServiceProvider.CognitoUserPool(
        poolData
      )
    } catch (error) {
      // console.log(error);
    }

    const cognitoUser = new AWSCognito.CognitoIdentityServiceProvider.CognitoUser(
      {
        Username: username,
        Pool: userPool
      }
    )

    // call forgotPassword on cognitoUser
    cognitoUser.forgotPassword({
      onSuccess() {
        resolve()
      },
      onFailure(err: any) {
        reject(err)
      }
    })
  })
}
