import { fetchWrap } from './fetch'
import { IProgramInfo } from '@/utils/interface'
import { getToken, getSession } from '@/utils/auth'
/*
 * program
 */
export function tagTextAutocomplete(name: string) {
  const url = `${process.env.VUE_APP_BASE_URL}/programtag/autocomplete?text=${name}`
  return fetchWrap({ url })
}

export function fetchPrograms(
  page = 1,
  isDesc = true,
  orderBy = 'updatedTime',
  status = 'public',
  query?: string
) {
  let url =
    `${process.env.VUE_APP_BASE_URL}/programs?` +
    `orderBy=${orderBy}&isDesc=${isDesc}&page=${page}&status=${status}`
  if (query) {
    url += `&query=${encodeURIComponent(query)}`
  }
  return fetchWrap({ url })
}

