/** @description upload program info pdf
 * @param {object} data
 * @return promise
 */
export async function uploadProgramInfoPdf(data: FormData) {
  await getSession()
  return new Promise((resolve: any, reject: any) => {
    const xhr = new XMLHttpRequest()
    xhr.withCredentials = false
    xhr.addEventListener('readystatechange', function() {
      if (this.readyState === 4) {
        if (xhr.status === 201) {
          resolve(this.responseText)
        } else {
          const response = JSON.parse(xhr.response)
          const error = response.error
          const str = `${error.message ? error.message : error.debug}`
          reject(str)
        }
      }
    })

    xhr.open('POST', `${process.env.VUE_APP_BASE_URL}/programinfos/uploadpdf`)
    xhr.setRequestHeader('Authorization', `${getToken()}`)
    xhr.send(data)
  })
}

