import { fetchWrap } from './fetch'

export function fetchOrganizerList(
  page = 1,
  isDesc = true,
  orderBy = 'updatedTime',
  query?: string
) {
  let url =
    `${process.env.VUE_APP_BASE_URL}/organizers?` +
    `orderBy=${orderBy}&isDesc=${isDesc}&page=${page}`
  if (query) {
    url += `&query=${encodeURIComponent(query)}`
  }
  return fetchWrap({ url })
}

export function createOrganizer(data: object) {
  const options = {
    method: 'POST',
    body: JSON.stringify(data),
    headers: new Headers({ 'Content-Type': 'application/json' })
  }
  const url = `${process.env.VUE_APP_BASE_URL}/organizers`
  return fetchWrap({ url, options })
}

