import { fetchWrap } from './fetch'
/*
 * price plane
 */

/*
id: event id
*/
export function fetchPriceplanes(id: string) {
  const url = `${process.env.VUE_APP_BASE_URL}/priceplans?eventId=${id}`
  return fetchWrap({ url })
}

/*
id: priceplane id
*/
export function fetchPriceplane(id: string) {
  const url = `${process.env.VUE_APP_BASE_URL}/priceplans/${id}`
  return fetchWrap({ url })
}

export function createPriceplane(data: object) {
  const options = {
    method: 'POST',
    body: JSON.stringify(data),
    headers: new Headers({ 'Content-Type': 'application/json' })
  }
  const url = `${process.env.VUE_APP_BASE_URL}/priceplans`
  return fetchWrap({ url, options })
}

export function updatePriceplane(id: string, data: object) {
  const options = {
    method: 'PUT',
    body: JSON.stringify(data),
    headers: new Headers({ 'Content-Type': 'application/json' })
  }
  const url = `${process.env.VUE_APP_BASE_URL}/priceplans/${id}`
  return fetchWrap({ url, options })
}

export function fetchPriceplaneTemplate() {
  const url = `${process.env.VUE_APP_BASE_URL}/priceplans/template`
  return fetchWrap({ url })
}

export function deletePriceplane(id: string) {
  const options = {
    method: 'DELETE',
    headers: new Headers({ 'Content-Type': 'application/json' })
  }
  const url = `${process.env.VUE_APP_BASE_URL}/priceplans/${id}`
  return fetchWrap({ url, options })
}

export function copyPriceplane(data: object) {
  const options = {
    method: 'POST',
    body: JSON.stringify(data),
    headers: new Headers({ 'Content-Type': 'application/json' })
  }
  const url = `${process.env.VUE_APP_BASE_URL}/priceplans/copy`
  return fetchWrap({ url, options })
}

export function fetchPriceplanesSectionMapping(priceplaneId: string) {
  const url = `${process.env.VUE_APP_BASE_URL}/priceplans/priceplansectionmapping/${priceplaneId}}`
  return fetchWrap({ url })
}

/** @description 取得常用折扣方案清單
 * @return promise
 */
export function fetchFrequentlyusedPriceplans() {
  const url = `${process.env.VUE_APP_BASE_URL}/priceplans/frequentlyusedpriceplans`
  return fetchWrap({ url })
}

/** @description 刪除常用折扣方案
 * @param {object} data
 * @return promise
 */
export function deleteFrequentlyusedPriceplan(
  frequentlyUsedPricePlanId: string
) {
  const url = `${process.env.VUE_APP_BASE_URL}/priceplans/frequentlyusedpriceplans/${frequentlyUsedPricePlanId}`
  const options = {
    method: 'DELETE',
    headers: new Headers({ 'Content-Type': 'application/json' })
  }
  return fetchWrap({ url, options })
}

/** @description 建立常用折扣方案
 * @param {object} {'name', 'eventId'}
 * @return promise
 */
export function createFrequentlyusedPriceplans(data: {
  name: string
  eventId: string
}) {
  const url = `${process.env.VUE_APP_BASE_URL}/priceplans/frequentlyusedpriceplans`
  const options = {
    method: 'POST',
    body: JSON.stringify(data),
    headers: new Headers({ 'Content-Type': 'application/json' })
  }
  return fetchWrap({ url, options })
}

/** @description 取得常用折扣方案
 * @param {object} {'eventId', 'frequentlyUsedPricePlanId'}
 * @return promise
 */
export function fetchFrequentlyusedPriceplan(data: {
  eventId: string
  frequentlyUsedPricePlanId: string
}) {
  const url = `${process.env.VUE_APP_BASE_URL}/priceplans/loadfrequentlyusedpriceplan`
  const options = {
    method: 'POST',
    body: JSON.stringify(data),
    headers: new Headers({ 'Content-Type': 'application/json' })
  }
  return fetchWrap({ url, options })
}
