import { fetchWrap } from './fetch'
/*
展演場地
*/
export function fetchEventVenueRelationData() {
  const url = `${process.env.VUE_APP_BASE_URL}/eventvenues/relationdata`
  return fetchWrap({ url })
}

export function createEventvenues(data: object) {
  const options = {
    method: 'POST',
    body: JSON.stringify(data),
    headers: new Headers({ 'Content-Type': 'application/json' })
  }
  const url = `${process.env.VUE_APP_BASE_URL}/eventvenues`
  return fetchWrap({ url, options })
}

export function updateEventvenues(id: string, data: object) {
  const options = {
    method: 'PUT',
    body: JSON.stringify(data),
    headers: new Headers({ 'Content-Type': 'application/json' })
  }

  const url = `${process.env.VUE_APP_BASE_URL}/eventvenues/${id}`
  return fetchWrap({ url, options })
}

export function fetchEventVenues(id: string) {
  const url = `${process.env.VUE_APP_BASE_URL}/eventvenues?programId=${id}`
  return fetchWrap({ url })
}

export function fetchEventVenue(id: string) {
  const url = `${process.env.VUE_APP_BASE_URL}/eventvenues/${id}`
  return fetchWrap({ url })
}

export function deleteEventVenue(id: string) {
  const options = {
    method: 'DELETE',
    headers: new Headers({ 'Content-Type': 'application/json' })
  }
  const url = `${process.env.VUE_APP_BASE_URL}/eventvenues/${id}`
  return fetchWrap({ url, options })
}

export function copyEventVenue(data: object) {
  const options = {
    method: 'POST',
    body: JSON.stringify(data),
    headers: new Headers({ 'Content-Type': 'application/json' })
  }
  const url = `${process.env.VUE_APP_BASE_URL}/eventvenues/copy`
  return fetchWrap({ url, options })
}
