/** @description 建立票區
 * @param {object} data
 * @return promise
 */
export function createSection(data: object) {
  const options = {
    method: 'POST',
    body: JSON.stringify(data),
    headers: new Headers({ 'Content-Type': 'application/json' })
  }
  const url = `${process.env.VUE_APP_BASE_URL}/sections`
  return fetchWrap({ url, options })
}

