/** @description 上傳子圖
 * @param {object} data
 * @return promise
 */
export async function updateSeatFile(data: FormData) {
  // const options = {
  //   method: 'POST',
  //   body: data,
  //   // mode: 'no-cors',
  //   headers: new Headers({ 'mimeType': 'multipart/form-data' })
  // }
  // const url = `${process.env.VUE_APP_BASE_URL}/sections/upload`
  // return fetchWrap({ url, options })
  await getSession()
  return new Promise((resolve: any) => {
    const xhr = new XMLHttpRequest()
    xhr.withCredentials = false
    xhr.addEventListener('readystatechange', function() {
      if (this.readyState === 4) {
        resolve(this.responseText)
      }
    })

    xhr.open('POST', `${process.env.VUE_APP_BASE_URL}/sections/upload`)
    // xhr.setRequestHeader('Content-Type', 'form-data')
    // xhr.setRequestHeader('cache-control', 'no-cache')
    // xhr.setRequestHeader('Postman-Token', '31756027-a000-425e-b7fb-54e76fdaf526')
    xhr.setRequestHeader('Authorization', `${getToken()}`)
    xhr.send(data)
  })
}

