import { fetchWrap } from './fetch'
/*
 * venue
 */
export function checkVenueName(data: object) {
  const options = {
    method: 'POST',
    body: JSON.stringify(data),
    headers: new Headers({ 'Content-Type': 'application/json' })
  }
  const url = `${process.env.VUE_APP_BASE_URL}/venues/checkname`
  return fetchWrap({ url, options })
}

export function fetchVenues(
  page = 1,
  isDesc = true,
  orderBy = 'updatedTime',
  query?: string
) {
  let url = `${process.env.VUE_APP_BASE_URL}/venues?orderBy=${orderBy}&isDesc=${isDesc}&page=${page}`
  if (query) {
    url += `&query=${encodeURIComponent(query)}`
  }
  return fetchWrap({ url })
}

export function createVenue(data: object) {
  const options = {
    method: 'POST',
    body: JSON.stringify(data),
    headers: new Headers({ 'Content-Type': 'application/json' })
  }
  const url = `${process.env.VUE_APP_BASE_URL}/venues`
  return fetchWrap({ url, options })
}

export function fetchVenuesById(id: string) {
  const url = `${process.env.VUE_APP_BASE_URL}/venues/${id}`
  return fetchWrap({ url })
}

export function updateVenue(id: string, data: object) {
  const options = {
    method: 'PUT',
    body: JSON.stringify(data),
    headers: new Headers({ 'Content-Type': 'application/json' })
  }
  const url = `${process.env.VUE_APP_BASE_URL}/venues/${id}`
  return fetchWrap({ url, options })
}

export function searchVenuesByName(name: string) {
  const url = `${process.env.VUE_APP_BASE_URL}/venues/search?text=${name}`
  return fetchWrap({ url })
}

export function fetchRelationdata() {
  const url = `${process.env.VUE_APP_BASE_URL}/venues/relationdata`
  return fetchWrap({ url })
}
