import { fetchWrap } from './fetch'
/*
 * member
 */

/*
 * operators
 */
export function fetchAcoperators(
  page = 1,
  isDesc = true,
  orderBy = 'account',
  query?: string
) {
  let url =
    `${process.env.VUE_APP_BASE_URL}/acoperators?` +
    `orderBy=${orderBy}&isDesc=${isDesc}&page=${page}`
  if (query) {
    url += `&query=${encodeURIComponent(query)}`
  }
  return fetchWrap({ url })
}

export function fetchAcoperator(account: string) {
  const url = `${process.env.VUE_APP_BASE_URL}/acoperators/operator?ac=${account}`
  return fetchWrap({ url })
}

export function fetchAcoperatorMe() {
  const url = `${process.env.VUE_APP_BASE_URL}/acoperators/me`
  return fetchWrap({ url })
}

export function updateAcoperator(account: string, data: object) {
  const options = {
    method: 'PUT',
    body: JSON.stringify(data),
    headers: new Headers({ 'Content-Type': 'application/json' })
  }
  const url = `${process.env.VUE_APP_BASE_URL}/acoperators/operator?ac=${account}`
  return fetchWrap({ url, options })
}

