/** @description 場次暫停（回復）銷售
 * @param {string} eventId
 * @param {string} data {"isPaused: true"}
 * @return promise
 */
export function pauseEvent(eventId: string, data: any) {
  const options = {
    method: 'PUT',
    body: JSON.stringify(data),
    headers: new Headers({ 'Content-Type': 'application/json' })
  }
  const url = `${process.env.VUE_APP_BASE_URL}/events/ispaused/${eventId}`
  return fetchWrap({ url, options })
}
