
































import { Vue, Component } from 'vue-property-decorator'
import { loginCognito, signoutCognito } from '@/utils/auth'
import apis, { LOGIN, LOGOUT } from './apis'
import { IApi } from './utils/interface'
@Component
export default class App extends Vue {
  private apiList = apis

  private enable = true // 能不能按start

  private async onStart() {
    this.apiList.forEach((element: IApi) => {
      element.status = 0
    })
    this.enable = false
    const mergePromise = async (obj: any) => {
      for (const item of obj) {
        try {
          item.status = 1
          if (item.apiName === LOGIN) {
            await loginCognito({
              username: 'bruce',
              password: 'Q22822281'
            })
          } else if (item.apiName === LOGOUT) {
            await signoutCognito()
          } else {
            if (item.execute) {
              await this.$store.dispatch(item.apiName)
            }
          }
          item.status = 3
          const id = document.getElementById(item.apiName)
          if (id) {
            id.scrollIntoView()
          }
        } catch (error) {
          item.status = 2
        }
      }
    }
    await mergePromise(apis)
    this.enable = true
  }

  private apiStatus(status: number) {
    if (status === 0 || status === 1) {
      return {
        borderColor: 'lightGray'
      }
    } else if (status === 2) {
      return {
        borderColor: 'red'
      }
    } else if (status === 3) {
      return {
        borderColor: 'green'
      }
    }
  }
}
