import { fetchWrap } from './fetch'
/** @description get ticket agreements
 * @param {number} page
 * @return promise
 */

export function fetchTicketAgreements(page = 1) {
  const url = `${process.env.VUE_APP_BASE_URL}/ticketagreements?orderBy=updatedTime&isDesc=true&page=${page}`
  return fetchWrap({ url })
}

/** @description get ticket agreement by id
 * @param {string} id
 * @return promise
 */
export function fetchTicketAgreement(id: string) {
  const url = `${process.env.VUE_APP_BASE_URL}/ticketagreements/${id}`
  return fetchWrap({ url })
}

/** @description create ticket agreement
 * @param {object} data
 * @return promise
 */
export function createTicketAgreement(data: object) {
  const options = {
    method: 'POST',
    body: JSON.stringify(data),
    headers: new Headers({ 'Content-Type': 'application/json' })
  }
  const url = `${process.env.VUE_APP_BASE_URL}/ticketagreements`
  return fetchWrap({ url, options })
}

/** @description update ticket agreement
 * @param {string} id
 * @param {object} data
 * @return promise
 */
export function updateTicketAgreement(id: string, data: object) {
  const options = {
    method: 'PUT',
    body: JSON.stringify(data),
    headers: new Headers({ 'Content-Type': 'application/json' })
  }
  const url = `${process.env.VUE_APP_BASE_URL}/ticketagreements/${id}`
  return fetchWrap({ url, options })
}

/** @description 檢查票價同意條款是否已存在
 * @param {object} data - {"name":"條款範本1"}.
 */
export function checkTicketAgreement(data: object) {
  const options = {
    method: 'POST',
    body: JSON.stringify(data),
    headers: new Headers({ 'Content-Type': 'application/json' })
  }

  const url = `${process.env.VUE_APP_BASE_URL}/ticketagreements/checkname`
  return fetchWrap({ url, options })
}

